import React from 'react';
import { Grid, Card, CardMedia, CardContent, Typography, Box } from '@mui/material';
import dishes from '../data/menuData';  // Correctly importing the data

const Menu = () => {
  return (
    <Box 
      sx={{ 
        flexGrow: 1, 
        padding: 3, 
        backgroundColor: '#34495e', // A deep charcoal gray for a professional look
        color: '#ecf0f1', // Light text for contrast
        minHeight: '100vh', // Ensure the background covers the whole page
      }}
    >
      <Typography variant="h4" component="h2" gutterBottom sx={{ textAlign: 'center', marginBottom: '20px', color: '#f39c12' }}>
        Explore Our Menu
      </Typography>
      <Grid container spacing={3}>
        {dishes.map((dish, index) => (
          <Grid item xs={12} sm={6} md={4} key={index}>
            <Card 
              sx={{ 
                maxWidth: 345, 
                m: 'auto', 
                bgcolor: 'background.paper', // Ensures contrast between the card and background
                '&:hover': { 
                  transform: 'scale(1.05)', // Enlarges card on hover
                  boxShadow: '0 0 20px rgba(0,0,0,0.8)' // Stronger shadow for 3D effect
                },
                transition: 'transform 0.3s ease-in-out' // Smooth transformation on hover
              }}
            >
              <CardMedia
                component="img"
                height="200"
                image={dish.image}
                alt={dish.name}
                sx={{
                  borderRadius: '4px', // Rounded corners for the image
                  border: '1px solid #bdc3c7' // Subtle border around the image
                }}
              />
              <CardContent sx={{ bgcolor: 'rgba(255, 255, 255, 0.9)' }}>
                <Typography gutterBottom variant="h5" component="h2" color="text.primary">
                  {dish.name}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {dish.description}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default Menu;
