import React, { useState } from 'react';
import { Box, TextField, Button, Typography, Snackbar, Grid, MenuItem, Select, FormControl, InputLabel } from '@mui/material';
import MuiAlert from '@mui/material/Alert';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const Reservations = () => {
  const [guests, setGuests] = useState('');
  const [guestType, setGuestType] = useState('');
  const [openError, setOpenError] = useState(false);
  const [openSuccess, setOpenSuccess] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    const numGuests = parseInt(guests, 10);
    if ((guestType === 'family' && numGuests > 10) || (guestType === 'visitor' && numGuests > 5)) {
      setOpenError(true);
    } else {
      setOpenSuccess(true);  // Trigger the success message
    }
  };

  const handleCloseError = (reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenError(false);
  };

  const handleCloseSuccess = (reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpenSuccess(false);
  };

  return (
    <Grid container sx={{ height: '100vh' }}>
      <Grid item xs={12} md={6} sx={{ backgroundImage: 'url("https://png.pngtree.com/png-vector/20190215/ourmid/pngtree-vector-location-icon-png-image_515424.jpg")', backgroundSize: 'cover', backgroundPosition: 'center' }}>
        <Typography variant="h5" sx={{ p: 3, color: 'white', backgroundColor: 'rgba(0, 0, 0, 0.5)' }}>Our Location</Typography>
        <Typography variant="body1" sx={{ p: 3, color: 'white', backgroundColor: 'rgba(0, 0, 0, 0.5)' }}>
          123 Main St, Lahore , Pakistan
        </Typography>
      </Grid>
      <Grid item xs={12} md={6} sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', backgroundColor: '#2c3e50' }}>
        <Box display="flex" flexDirection="column" alignItems="center" mt={4} sx={{ p: 3, width: '100%', maxWidth: 360, backgroundColor: '#ecf0f1', borderRadius: '8px' }}>
          <Typography variant="h4" gutterBottom>Book Your Table</Typography>
          <form onSubmit={handleSubmit} style={{ width: '100%' }}>
            <TextField label="Name" variant="outlined" fullWidth margin="normal" required autoFocus />
            <TextField label="Date" type="date" variant="outlined" fullWidth margin="normal" InputLabelProps={{ shrink: true }} required />
            <TextField label="Time" type="time" variant="outlined" fullWidth margin="normal" InputLabelProps={{ shrink: true }} required />
            <FormControl fullWidth margin="normal">
              <InputLabel id="guest-type-label">Guest Type</InputLabel>
              <Select
                labelId="guest-type-label"
                id="guest-type"
                value={guestType}
                label="Guest Type"
                onChange={e => setGuestType(e.target.value)}
                required
              >
                <MenuItem value="visitor">Visitor</MenuItem>
                <MenuItem value="family">Family</MenuItem>
              </Select>
            </FormControl>
            <TextField label="Number of Guests" type="number" variant="outlined" fullWidth margin="normal" value={guests} onChange={e => setGuests(e.target.value)} required />
            <Button type="submit" variant="contained" color="primary" fullWidth sx={{ mt: 2 }}>
              Make Reservation
            </Button>
          </form>
          <Snackbar open={openError} autoHideDuration={6000} onClose={handleCloseError}>
            <Alert onClose={handleCloseError} severity="error" sx={{ width: '100%' }}>
              {guestType === 'family' ? 'Maximum family members allowed are 10 only.' : 'Maximum visitors allowed are 5 only.'}
            </Alert>
          </Snackbar>
          <Snackbar open={openSuccess} autoHideDuration={6000} onClose={handleCloseSuccess}>
            <Alert onClose={handleCloseSuccess} severity="success" sx={{ width: '100%' }}>
              Your table has been booked Successfully. Happy dining!
            </Alert>
          </Snackbar>
        </Box>
      </Grid>
    </Grid>
  );
};

export default Reservations;
