import React from 'react';
import { Box, Typography, Button } from '@mui/material';
import { useNavigate } from 'react-router-dom'; // Importing useNavigate
import homeImage from '../assets/home-image.jpg'; // Path to your image file

const Home = () => {
  const navigate = useNavigate(); // Getting the navigate function

  // Function to handle navigation
  const handleNavigate = () => {
    navigate('/menu'); // Update this path to match your Menu page's route
  };

  return (
    <Box
      sx={{
        position: 'relative',
        textAlign: 'center',
        p: { xs: 2, sm: 4 },
      }}
    >
      <img
        src={homeImage}
        alt="Cozy dining room"
        style={{ width: '100%', height: 'auto', position: 'absolute', left: 0, top: 0, zIndex: -1 }}
      />
      <Box
        sx={{
          position: 'relative',
          zIndex: 2,
          color: '#fff',
          textShadow: '1px 1px 8px rgba(0,0,0,0.6)',
          maxWidth: '1200px',
          mx: 'auto',
          pt: 8,
          pb: 4
        }}
      >
        <Typography variant="h2" component="h1" gutterBottom>
          Welcome to Huzaifa's Culinary Haven
        </Typography>
        <Typography variant="h5" color="text.bold" paragraph>
          Experience the best culinary delights from around the world, all under one roof. Join us for a taste adventure!
        </Typography>
        <Button variant="contained" color="primary" size="large" sx={{ mt: 2 }} onClick={handleNavigate}>
          Explore Our Menu
        </Button>
      </Box>
    </Box>
  );
};

export default Home;
