import { createTheme } from '@mui/material/styles';

const theme = createTheme({
  palette: {
    primary: {
      main: '#556cd6',
    },
    secondary: {
      main: '#19857b',
    },
  },
  typography: {
    fontFamily: 'Arial, sans-serif',
    fontSize: 14,
    button: {
      textTransform: 'none' // Turns off uppercase transformation for buttons
    }
  },
});

export default theme;
