import React from 'react';
import { NavLink } from 'react-router-dom';
import { AppBar, Toolbar, Button, Typography } from '@mui/material';
import { styled } from '@mui/material/styles';

const StyledNavLink = styled(NavLink)(({ theme }) => ({
  color: 'white',  // Default color for the links
  textDecoration: 'none',  // Remove underline from links
  margin: theme.spacing(1),
  '&.active': {  // Styles for active link
    color: '#FFD700',  // Change color when active
  }
}));

const Navigation = () => {
  return (
    <AppBar position="static" sx={{ background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)' }}>
      <Toolbar sx={{ justifyContent: 'space-between' }}>
        <StyledNavLink to="/" exact>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1, cursor: 'pointer' }}>
            Huzaifa's Culinary Haven
          </Typography>
        </StyledNavLink>
        <div>
          <StyledNavLink to="/" exact>
            <Button color="inherit" sx={{ fontWeight: 'bold' }}>
              Home
            </Button>
          </StyledNavLink>
          <StyledNavLink to="/menu">
            <Button color="inherit" sx={{ fontWeight: 'bold' }}>
              Menu
            </Button>
          </StyledNavLink>
          <StyledNavLink to="/reservations">
            <Button color="inherit" sx={{ fontWeight: 'bold' }}>
              Reservations
            </Button>
          </StyledNavLink>
        </div>
      </Toolbar>
    </AppBar>
  );
};

export default Navigation;
